//index.vue
<template>
  <div id="pageQuiButton">
    <div class="mod-module mod-btn">
      <p class="btn-bar text-r">
        <qui-btn class="small" msg="确定"></qui-btn>
      </p>
      <p class="btn-bar text-r">
        <qui-btn class="small bor"></qui-btn>
      </p>
      <p class="btn-bar text-r">
        <qui-btn class="small icon" msg="下载">
          <img
            class="ico"
            slot="icon"
            src="http://qzonestyle.gtimg.cn/aoi/sola/20170214175951_TA6qW1X7ob.png"
          />
        </qui-btn>
      </p>
      <p class="btn-bar">
        <qui-btn class="large bor" msg="按钮可以改文案"></qui-btn>
      </p>
      <p class="btn-bar">
        <qui-btn class="large icon bg" msg="大的按钮">
          <img
            class="ico"
            slot="icon"
            src="http://qzonestyle.gtimg.cn/aoi/sola/20170214175951_TA6qW1X7ob.png"
          />
        </qui-btn>
      </p>
      <p class="btn-bar bar-flex">
        <qui-btn class="middle icon bor" msg="取消"></qui-btn>
        <qui-btn class="middle icon bg" msg="确定"></qui-btn>
      </p>
      <p class="btn-bar bar-flex">
        <qui-btn class="middle icon"></qui-btn>
        <qui-btn class="middle" msg="原生点击" v-on:click.native="doTheClick">
          <!--原生的click而不是子组件的-->
          <img
            class="ico"
            slot="icon"
            src="http://qzonestyle.gtimg.cn/aoi/sola/20170214175951_TA6qW1X7ob.png"
          />
        </qui-btn>
        <qui-btn class="middle bg" msg="我可以点击" v-on:btnClickEvent="btnClickEvent"></qui-btn>
      </p>
    </div>
  </div>
</template>

<script>
import quiBtn from '../components/quiButton.vue'
export default {
  name: 'pageQuiButton',
  components: {
    'qui-btn': quiBtn
  },
  methods: {
    btnClickEvent: function() {
      alert('你点击了组件的click:btnClickEvent')
    },
    doTheClick: function() {
      alert('我是原生的click')
    }
  }
}
</script>

<style scoped>
.mod-module.mod-btn .bar-flex {
  display: -webkit-flex; /* Safari */
  display: flex;
}
.mod-module.mod-btn .btn-bar {
  padding: 5px 12px;
  font-size: 0;
  text-align: center;
}
.mod-module.mod-btn .text-r {
  text-align: right;
}
</style>



